
import { Injectable, Output, EventEmitter } from '@angular/core';
import { PopupDataDetail } from './popup-data-detail';

@Injectable()
export class Dataservice {

  public popupDataDetail: PopupDataDetail;  
  

  @Output() change: EventEmitter<PopupDataDetail> = new EventEmitter();

  modifyData(_detailId: number, _detaileImage: string, _shortDesc:string , _detaileDesc: string) {
    if (this.popupDataDetail == null)
       this.popupDataDetail = new PopupDataDetail(_detailId,_detaileImage, _shortDesc, _detaileDesc);
    this.popupDataDetail.detaileId = _detailId;
    this.popupDataDetail.detaileImage = _detaileImage;
    this.popupDataDetail.shortDesc = _shortDesc;
    this.popupDataDetail.detaileDesc = _detaileDesc;
    this.change.emit(this.popupDataDetail);    
  }

}
