import { Component,Input, OnInit } from '@angular/core';
import {Portfolio} from './portfolio';
import {PortfolioComponent} from "./portfolio.component";
import {Router} from "@angular/router";
import { Dataservice } from '../dataservice';

@Component({
  selector: 'app-project-card',
  templateUrl: './project-card.component.html',  
  styles: [ ]
})
export class ProjectCardComponent implements OnInit {
  @Input() portfolio: Portfolio;
  portfoliosComponent: PortfolioComponent = new PortfolioComponent();
  constructor(private router: Router, private dataService: Dataservice) { }

  ngOnInit() {
  }

  public setProject(portfolioId: number) {
    console.log(portfolioId);
    localStorage.setItem("portfolio", JSON.stringify({"portfolio": this.portfoliosComponent.portfolios[portfolioId]}));
    this.router.navigate(["project-details"]);
  }

  public modifyPopupData(serviceId: number, imageUrl: string , shotDesc:string, serviceDesc: string) {
    this.dataService.modifyData(serviceId,imageUrl,shotDesc,serviceDesc);    
    }

}
