import { Component, OnInit } from '@angular/core';
import {Validators, FormBuilder, FormGroup} from "@angular/forms";
import {Http, Headers, RequestOptions} from "@angular/http";
import "rxjs/add/operator/map";

@Component({
  selector: 'app-contactus',
  templateUrl: './contactus.component.html',
  styles: []
})
export class ContactusComponent implements OnInit {
  formGroup: FormGroup;
  constructor(private formBuilder: FormBuilder, private http: Http) { } 
  formSubmitted = false;
  mailSend = false;
  message = "";

  buildForm(): void {
    this.formGroup = this.formBuilder.group({
      "email": [null, [Validators.required, Validators.email]],
      "subject": [null, [Validators.required]],
      "description": [null, [Validators.required]],
      "validate" : ""
    });

  } 

  ngOnInit() {
    this.buildForm();
  }

  onSubmitForm() {
    this.mailSend = false;
    this.message = "";
    if (this.formGroup.valid) {
      this.sendMail(this.formGroup.get("email").value, this.formGroup.get("subject").value, this.formGroup.get("description").value);
      this.formSubmitted = true;
      this.formGroup.reset();
    }else {
      this.mailSend = true;
      this.message = "Please fill all  fields";
      return;
    }
  }

  public sendMail(email: String, subject: String, queryDescription: String) {
    const headers = new Headers({ "Content-Type": "application/json" });
    const options = new RequestOptions({ headers: headers });

    this.http.post("https://techzolainc.herokuapp.com/send", JSON.stringify({"to": "sardarrahyeem@gmail.com; hamidsayeedkhan@gmail.com; engineer_mubarak@hotmail.com", "from": `${email}`, "subject": subject, "description": queryDescription}), options)
      .subscribe(
        (data) => 
        {
          console.log(data);
          if (data.text() === "sent") 
          {
            this.mailSend = true;
            this.message = "Your Query has been sent";
          }
          else 
          {
            this.message = "Your Query has been sent";
          }
        }
      );
  }

}
