import { Component,Input, OnInit } from '@angular/core';
import {Employee} from "./employee";
import {OurGroupComponent} from "./our-group.component";
import {Router} from "@angular/router";

@Component({
  selector: 'app-group-detail',
  templateUrl: './group-detail.component.html',
  styles: []
})
export class GroupDetailComponent implements OnInit {

  @Input() employee: Employee;
  employeeId: number;
  ourGroupComponent: OurGroupComponent = new OurGroupComponent();

  constructor(private router: Router) {
    localStorage.clear();
  }

  ngOnInit() {
  }

  public setEmployee(employeeId: number) {
    this.employeeId = employeeId;
    console.log(this.employeeId);
    localStorage.setItem("employee", JSON.stringify({"employee": this.ourGroupComponent.employees[this.employeeId]}));
    this.router.navigate(["emp-details"]);
  }

}
