import { Component, Input, OnInit } from '@angular/core';
import { Service } from './service';
import { ServicesComponent } from './services.component';
import {Router} from "@angular/router";
import { Dataservice } from '../dataservice';

@Component({
  selector: 'app-service-card',
  templateUrl: './service-card.component.html',
  styles: []
})
export class ServiceCardComponent implements OnInit {
  @Input() service: Service;
  servicesComponent: ServicesComponent= new ServicesComponent();
  constructor(private router: Router, private dataService: Dataservice) { }

 

  ngOnInit() {
  }

  public setService(serviceId: number) {
    console.log(serviceId);
    localStorage.setItem("service", JSON.stringify({"service": this.servicesComponent.services[serviceId]}));
    this.router.navigate(["service-details"]);
  }

  public modifyPopupData(serviceId: number, imageUrl: string , shortDesc, serviceDesc: string) {
    this.dataService.modifyData(serviceId,imageUrl,shortDesc,serviceDesc);    
    }
  
  

}
